@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
* {
  box-sizing: border-box;
}

.row{position:relative;margin-bottom:2.5rem}.row:hover .row__title span,.row:hover .row__title svg{transform:translate(0, 0);opacity:1}.row:hover .custom_next,.row:hover .custom_prev{opacity:1}.row__title{margin-left:2rem;cursor:pointer;color:#eee}.row__title a,.row__title a:hover{color:currentColor;text-decoration:unset}.row__title h2{font-size:1.4rem;text-transform:uppercase;font-weight:600;display:flex;align-items:center}.row__title span,.row__title svg{font-size:.8rem;opacity:0;transform:translate(-6rem, 0)}.row__title span{text-transform:capitalize;transition:all .6s cubic-bezier(0.075, 0.82, 0.165, 1);margin-left:10px}.row__title svg{transition:all .4s cubic-bezier(0.075, 0.82, 0.165, 1);margin-left:5px}.row__posters{display:flex;overflow-y:hidden;overflow-x:auto;margin-left:2rem;margin-top:20px}.row__posters::-webkit-scrollbar{display:none}.row__poster{object-fit:cover;width:100%;height:100%;margin-right:10px}.row__poster--large{max-height:250px}.swiper .custom_next,.swiper .custom_prev{position:absolute;width:3rem;z-index:99;top:0;height:100%;display:flex;align-items:center;justify-content:center;cursor:pointer;opacity:0;transition:all .2s ease;background-color:rgba(20,20,20,.5)}.swiper .custom_next svg,.swiper .custom_prev svg{transition:all .2s ease;font-size:2rem;color:#fff}.swiper .custom_next:hover,.swiper .custom_prev:hover{background-color:rgba(20,20,20,.7)}.swiper .custom_next:hover svg,.swiper .custom_prev:hover svg{font-size:3rem}.swiper .custom_next{right:0}.swiper .custom_prev{left:0}.swiper{overflow:visible}.swiper-slide{width:230px !important;height:130px;transition:transform 400ms;cursor:pointer}.swiper-slide:hover{transform:scale(2);z-index:9999;position:relative}
.banner{width:100%;height:100vh;background-size:cover;background-position:center 8%;margin-bottom:-2rem;color:#fff;position:relative;display:flex;align-items:end;padding-bottom:18vh}.banner_title{font-size:4rem;font-weight:800;padding-bottom:.3rem}.banner_contents{margin-left:3rem;display:flex;flex-direction:column;justify-content:end}.banner_description{max-width:45rem;line-height:1.3;padding-top:1rem;font-size:20px;font-weight:500;transition:all .5s ease}.banner_description.height-0{height:0;overflow:hidden;padding:0}.banner .banner__buttons{margin-top:1.5rem;display:flex;align-items:center}.banner_button{cursor:pointer;color:#000;outline:none;border:none;font-weight:700;border-radius:.2vw;padding-left:1.5rem;padding-right:1.5rem;margin-right:.7rem;padding-top:10px;padding-bottom:10px;background-color:#fff;display:flex;align-items:center;justify-content:center;font-size:15px;font-weight:700;transition:all .1s}.banner_button:hover{color:#000;background-color:rgba(255,255,255,.75)}.banner_button svg{margin-right:10px;font-size:20px}.banner_button.transparent{background-color:rgba(109,109,110,.7);color:#fff}.banner_button.transparent:hover{background-color:rgba(109,109,110,.4)}.banner--fadeBottom{height:7.4rem;background-image:linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);position:absolute;bottom:0;left:0;width:100%}
.nav{position:fixed;width:100%;display:flex;justify-content:space-between;align-items:center;z-index:1;padding:20px;height:68px;transition:all .5s ease-in}.nav_black{background-color:#111}.nav_logo{position:fixed;left:3rem}.nav_logo img{width:80px}.nav_avatar{width:30px;object-fit:contain;position:fixed;right:20px}
.moviesList{display:flex;align-items:center;flex-wrap:wrap;padding:6rem 3rem;padding-bottom:1rem}.moviesList img{width:20%;padding-right:9px;height:140px;object-fit:cover;margin-bottom:3.5rem}
html {
  overflow-x: hidden;
}

* {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}


.app {
  background-color: #111;
  min-height: 100vh;
  padding-bottom: 1rem;
}


